.blocker {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    z-index: 99;
    padding: 20px;
    box-sizing: border-box;
    background-color: rgb(0,0,0);
    background-color: rgba(42, 107, 70,0.75);
    text-align: center; }

.modal {
    display: none;
    vertical-align: middle;
    position: relative;
    z-index: 2;
    box-sizing: border-box;
    width: 100%;
    max-width: 445px;
    background: #fff;
    -webkit-box-shadow: 0 0 10px #2a6b46;
    -moz-box-shadow: 0 0 10px #2a6b46;
    -o-box-shadow: 0 0 10px #2a6b46;
    -ms-box-shadow: 0 0 10px #2a6b46;
    box-shadow: 0 0 10px #2a6b46;
    text-align: left; }

.modal a.close-modal {
  position: absolute;
  top: -12.5px;
  right: -12.5px;
  display: block;
  width: 30px;
  height: 30px;
  text-indent: -9999px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url('../img/close.svg'); }

.blocker:before {
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-right: -0.05em; }

.leadership-name {
  font-weight: 600;
  font-size: 22px; }

.leadership-description {
  margin-top: 10px;
  text-align: justify; }

.modal-image-container {
  display: flex;
  justify-content: center; }

.modal-application-img {
  width: 100%;
  height: 100%;
  img {
    display: block; } }
