.calibration-services {
  padding: 25px 0px;
  text-align: center;
  p {
    padding-bottom: 15px; }


  &__image {
    background-image: url(../img/calibration-services.png);
    background-position: center center;
    height: 285px;
    background-size: cover; } }

.calibration {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include r(925) {
    flex-direction: column; }

  h2 ,h3 {
    margin-bottom: 40px;
    text-transform: uppercase;
    text-align: left; }

  &__image {
    margin-right: 20px;
    @include r(925) {
      margin-right: 0; } }

  &__text {
    max-width: 690px; } }

.calibration-laboratory {
  h2, h3 {
    text-transform: uppercase;
    text-align: left; }

  p {
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0; } } }

.calibration {
  &__buttons {
    display: flex;
    justify-content: center;
    margin-top: 30px;
    flex-wrap: wrap;
    @include r(910) {
      flex-direction: column; }

    & > a {
      background-color: rgba(184, 150, 94, 0.2);
      margin-right: 20px;
      @include r(910) {
        max-width: 600px;
        width: 100%;
        margin: 20px auto 0; }
      &:last-child {
        margin-right: 0;
        @include r(910) {
          margin-right: auto; } } } } }

.calibration-pricing {
  padding: 0;
  text-align: center;
  h3 {
    margin-bottom: 0; } }

.calibration-service {
  padding: 0;
  h2, h3 {
    text-align: left;
    margin-bottom: 0;
    text-transform: uppercase; }

  table {
    margin-top: 30px; } }

.calibration-certificate {
  h3 {
    margin-bottom: 0; } }
