$xs : 576px;
$sm : 768px;
$md : 992px;
$lg : 1200px;

@media screen and (max-width: $lg) {

    .top {
        &__title {
            text-align: center; }
        &__span-text {
            display: block; } }
    .nav {
        &__item {
            & > a {
                font-size: 12px; }
            & > ul {
                & > li {
                    & > a {
                        font-size: 12px; } } } } }
    //product-block
    .goods {
        &__items {
            justify-content: space-around; } }

    .slider {
        &__wrapper {
            width: 90%; }

        &__block {
            &__arrow-left {
                left: 0; }
            &__arrow-right {
                right: 0; } } }

    .fields-activity {
        &__list {
            justify-content: space-around; }

        &__activity {
            margin-bottom: 30px; } }


    .testimonials {
        &__wrap {
            width: 90%; }

        &__arrow {
            bottom: 50%;
            top: initial; }

        .arrow-left {
            left: 0; }
        .arrow-right {
            right: 0; }

        &__item {
            &:nth-child(2) {
                margin: 0 15px; } } }

    .about {
        .testimonials {
            &__arrow {
                bottom: -70px;
                top: initial; } } }

    .products {
        &__list {
            justify-content: space-around; } }

    .filters {
        margin-right: 4%;
        margin-left: 3%; }


    .product {
        &__wrap {
            margin: 0 35px; } }

    .gallery {
        .photo-small {
            width: 50%;
            overflow: hidden; } }


    .reviews {
        &__review {
            margin: 0 15px; } }


    .related-block {
        &__items {
            flex-wrap: wrap;
            justify-content: space-around; } } }

@media screen and (max-width: 1090px) {
    .header {
        .menu {
            display: block; }
        &__nav {
            position: absolute;
            height: 100vh;
            top: 0;
            width: 320px;
            left: -320px;
            background-color: rgba(255,255,255, 0.9);
            max-width: 320px;
            padding: 30px 15px 0;
            transition: left 350ms cubic-bezier(.55,0,.1,1);
            flex-direction: column;
            z-index: 999; }
        &__logo {
            margin-left: 0; } }
    .nav {
        &__list {
            display: block;
            width: 100%;
            transform: translate3d(0px, 0px, 0px);
            transition: transform 350ms cubic-bezier(0.55, 0, 0.1, 1); }

        &__item {
            position: static;
            margin-right: 0;
            &:hover {
                ul {
                    visibility: hidden; } }
            & > a {
                color: #B3874C;
                display: block;
                word-wrap: break-word;
                width: 100%;
                font-size: 28px;
                padding: 15px 20px;
                border: 1px solid transparent;
                transition: background 350ms cubic-bezier(.55,0,.1,1), text-shadow 350ms cubic-bezier(.55,0,.1,1);
                &:after {
                    display: none; }
                &:hover {
                    transition: background 350ms cubic-bezier(0.55, 0, 0.1, 1), text-shadow 350ms cubic-bezier(0.55, 0, 0.1, 1);
                    text-shadow: 0 0 5px rgba(0, 0, 0, 0.16);
                    border: 1px solid #B3874C;
                    border-radius: 4px; } }

            & > ul {
                background-color: initial;
                width: 100%;
                transform: translate3d(640px, 0px, 0);
                border: initial;
                display: none;
                & > li {
                    & > a {
                        color: #B3874C;
                        display: block;
                        width: 100%;
                        font-size: 30px;
                        white-space: normal;
                        word-break: break-word;
                        margin-right: 0;
                        padding: 15px 20px;
                        border: 1px solid transparent;
                        transition: background 350ms cubic-bezier(.55,0,.1,1), text-shadow 350ms cubic-bezier(.55,0,.1,1);
                        &:hover {
                            transition: background 350ms cubic-bezier(.55,0,.1,1), text-shadow 350ms cubic-bezier(.55,0,.1,1);
                            text-shadow: 0 0 5px rgba(0, 0, 0, 0.16);
                            border: 1px solid #B3874C;
                            border-radius: 4px;
                            color: #B3874C; } } } } }


        &__search {
            display: none; } }

    .item-bordered {
        padding: 0 !important;
        word-spacing: 100vw; } }

@media screen and (max-width: 1030px) {
    .about {
        .features {
            &__item {
                margin-bottom: 0;
                flex-direction: column;
                align-items: center;
                img {
                    margin-bottom: 30px; } }

            &__text {
                padding: 15px 60px;
                &:after {
                    display: none; } } } }

    .reward {
        &__wrap {
            max-width: initial;
            width: 44%; } } }




@media screen and (max-width: $md) {

    //header
    .top {
        &__block {
            flex-wrap: wrap;
            justify-content: center; }
        &__text {
            width: 100%;
            margin-right: 0;
            margin-top: 40px;
            text-align: center; } }

    .testimonials {
        &__item {
            width: 33%; } }

    .product {
        &__wrap {
            margin-bottom: 40px;
            max-width: 90%;
            margin-top: 30px; }
        &__sliders {
            .sliders {
                &__slide {
                    &:nth-child(even) {
                        &:hover {
                            border: 1px solid #B3874C; } } } } }

        &__block {
            flex-direction: column;
            align-items: center; }
        &__text {
            max-width: 100%; }

        &__buttons {
            max-width: 50%;
            margin: 0 auto;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;

            .buttons {
                &__link {
                    width: 45%; } } } }
    //main product
    .products {
        .list {
            &__item {
                max-width: 250px; } } }

    .reviews {
        &__block {
            flex-wrap: wrap;
            justify-content: space-around; }

        &__video {
            margin-top: 40px; } }

    //about page
    .text {
        text-align: center;
        &:first-child {
            margin-right: 50px; } } }

@media screen and (max-width: 925px) {

    .header {
        &__logo {
            margin-right: 15px;
            font-size: 0; } }

    .features {
        &__item {
            // flex-direction: column
            // height: initial
            p {
                &:after {
                    display: none; } } }
        &__img {
            max-width: 450px;
            width: 100%;
            margin: 0 auto 30px; }

        &__btn {
            margin-top: 40px; } } }



@media screen and (max-width: 825px) {
    .footer {
        &__block {
            width: 100%; }
        ul {
            flex-direction: column;
            align-items: center;
            & > li {
                margin-right: 0;
                margin-bottom: 10px;
                & > a {
                    color: #153623; } } } }
    .social-list {
        text-align: center; } }

@media screen and (max-width: $sm) {

    .header {
        .typed-cursor {
            display: none; } }


    .features {
        &__items-item {
                img {
                    width: 90%; } } }

    .reviews {
        &__block {
            flex-direction: column;
            align-items: center; }

        &__software {
            margin-bottom: 40px; }

        &__review {
            max-width: 80%;
            margin: 0 auto; }

        &__video {
            margin-top: 40px; } }

    .products {
        padding: 90px 15px 0;
        &__block {
            flex-direction: column;
            align-items: center; } }

    .filters {
        position: fixed;
        z-index: 99;
        background: #fff;
        width: 100%;
        padding: 0 10px;
        margin: 0;
        top: 100px;
        left: 0;

        &__wrap {
            max-width: 450px;
            margin: 100px auto 25px;
            height: auto;
            max-height: 50vh;
            overflow: hidden;
            overflow-y: auto;
            padding: 0 15px;
            display: none;
            @include scrollbar() {
                background-color: #F3F3F3;
                width: 5px; }
            @include scrollbar-track() {
                width: 5px;
                background-color: #F3F3F3; }
            @include scrollbar-thumb() {
                width: 5px;
                height: 20px;
                background-color: #326197; } }
        &__title {
            display: block;
            position: fixed;
            width: 100%;
            left: 0;
            background: #fff;
            border-bottom: 1px solid #2A6B46;
            text-align: center;
            top: 100px;
            z-index: 50;
            cursor: pointer;
            padding: 17px 0;
            i {
                transform: rotate(45deg) translateX(-50%);
                border: solid #636363;
                border-width: 0 3px 3px 0;
                display: inline-block;
                padding: 3px;
                margin-left: 10px; } } }
    .categories {
        &__title {
            font-size: 17px;
            font-weight: 900;
            color: #2A6B46;
            margin-bottom: 13px;
            line-height: 27px; } }

    //reward block
    .frost-sulivan {
        &__line {
            left: 10px; } }
    .reward {
        &__wrap {
            width: 60%;
            margin: 0 auto 50px; } }

    //about page
    .block-text {
        &__wrap {
            flex-direction: column; } }

    .about-text {
        &__block {
            flex-direction: column;
            & > div {
                width: 100%;
                max-width: 100%;
                margin: 0 auto 30px;
                &:last-child {
                    margin-bottom: 0; } } } }


    .form {
        &__wrap {
            flex-direction: column;

            input {
                max-width: 100%; } } } }

@media screen and (max-width: $xs) {
    //header
    .top {
        padding-bottom: 50px;
        &__image {
            display: none; } }

    .product {
        &__buttons {
            max-width: 100%; } }

    .breadcrumbs {
        &__list {
            flex-wrap: wrap; } }

    .products {
        &__block {
            flex-direction: column;
            align-items: center; }
        &__characteristics {
            margin-right: 0;
            margin-bottom: 40px; } }
    .filters {
        &__impedance {
            display: none; } }

    .vnas {
        &__item {
            margin: 15px; } } }
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    .title-checkbox {
        margin-left: 14px; } }

@-moz-document url-prefix() {
    span.title-checkbox {
        margin-left: 14px; } }


