$cubeSize: 3em;
$light: #ECECEC;
$dark: #34495e;

.loader {
  outline: 1px solid transparent;
  background-color: #2A6B46;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100%;
  .largeBox {
    height: $cubeSize;
    width: $cubeSize;
    background-color: #fff;
    outline: 1px solid transparent;
    position: fixed;
    animation: loaderAnim 1.25s alternate infinite ease-in-out; }

  .smallBox {
    height: $cubeSize;
    width: $cubeSize;
    background-color: #2A6B46;
    position: fixed;
    z-index: 1;
    outline: 1px solid transparent;
    animation: smallBoxAnim 1.25s alternate infinite ease-in-out; } }



@keyframes smallBoxAnim {
  0% {
    transform: scale(0.2) rotate(0deg); }
  100% {
    transform: scale(0.75) rotate(90deg); } }


@keyframes loaderAnim {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(90deg); } }
