.product {
    padding: 25px 15px;

    h2 {
        margin-bottom: 20px; }
    h3 {
        text-align: left;
        font-size: 12pt;
        color: #999999;
        margin-bottom: 20px;
        font-weight: 400; }

    &__block {
        display: flex;
        flex-direction: row;
        align-items: start;
        justify-content: space-between;
        border-bottom: 1px solid #2A6B46;
        padding-bottom: 40px; }
    &__title {
        font-weight: bold;
        color: $text-color;
        text-align: left;
        margin-bottom: 30px; }

    &__wrap {
        max-width: 460px; }

    &__text {
        color: $text-color;
        font-size: 12pt;
        line-height: 27px;
        margin-bottom: 35px; }


    &__list {
        li {
            color: $title-color;
            font-size: 12pt;
            line-height: 27px; } }

    &__buttons {
        display: flex;
        flex-direction: column;
        max-width: 242px;
        width: 100%; }

    &__link.btn {
        background-color: rgba(184, 150, 94, 0.2);
        color: #B3874C;
        width: 100%;
        margin-bottom: 25px;
        &:hover {
            color: #fff; }
        &:last-child {
            margin-bottom: 0; } }

    &__link {
        margin-bottom: 20px; }

    &__unit {
        display: flex;
        justify-content: space-between;
        align-items: center;

        p {
            color: $text-color;
            &:last-child {
                font-size: 14px;
                span {
                    font-size: 14px;
                    color: #A1A1A1; } } } }

    &__price-block {
        @include r(992) {
            margin: 0 auto; }
        strong {
            color: #2A6B46;
            font-size: 28px;
            display: block;
            text-align: center;
            margin: 15px auto; } } }
.gallery {
    max-width: 400px;
    width: 100%;

    &__photo, &__video, &__h5p {
        cursor: pointer;
        overflow: hidden;

        img {
            height: auto;
            width: 100%;
            overflow: hidden; } } }

.gallery__title {
	font-size: 16px;
	font-weight: bold;
	color: #4c4c4c;
	margin: 5px 0; }
.gallery__small {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start; }
.gallery__video, .gallery__h5p {
	position: relative; }
.photo-big {
    border: 1px solid #E3E3E3;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 399px;
    img {
        max-width: 396px; }
    &:hover {
        border: 1px solid #B3874C; }
    @include r(1200) {
        max-width: 100%; } }
.photo-small {
    width: 133px;
    height: 104px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: 1px solid #E3E3E3;
    border-right: 1px solid #E3E3E3;
    border-bottom: 1px solid #E3E3E3;
    border-top: 1px solid transparent;
    img {
        max-width: 130px;
        @include r(1200) {
            max-width: 198px; } }
    &:hover {
        border: 1px solid #B3874C; } }
.h5p-content-wrapper {
	max-width: 900px;
	margin: 0 auto;
	position: relative; }
.h5p-content-wrapper  button[type="button"].mfp-close {
	top: -40px;
	color: #fff;
	right: -6px;
	text-align: right;
	padding-right: 6px;
	width: 100%; }
