.our-products {
    background-color: rgb(249, 249, 249);
    padding-top: 0;

    .selected-product {
        .image-block {
            background: radial-gradient(ellipse at 50% 50%, rgba(179,135,76, 0.6) , rgba(179,135,76, 0) 75%); }
        p {
            opacity: 1; } }

    .container {
        position: relative; }

    .top-block {
        background-color: rgba(29, 69, 45, 0.75);
        height: 200px;
        width: 100%;
        padding-top: 20px;
        position: absolute;
        top: -200px;
        border-top-left-radius: 55px;
        border-top-right-radius: 55px;
        box-shadow: 6px -12px 65px -20px rgba(0,0,0,.5);
        @include r(1366) {
            height: 130px;
            top: -130px; }
        @include r(1049) {
            height: 235px;
            top: -235px; }
        @include r(586) {
            height: 240px;
            top: -240px; }
        @include r(539) {
            height: 335px;
            top: -335px; }
        @include r(370) {
            height: 360px;
            top: -360px; }


        .image-block {
            img {
                max-width: 170px; } } }

    &__btns {
        padding-top: 30px;
        @include r(600) {
            display: flex;
            flex-direction: column; }
        a {
            background-color: rgb(249, 249, 249);
            color: #b3874c;
            margin-right: 10px;
            padding: 0 10px;
            @include r(847);
            margin-bottom: 15px;
            @include r(600);
            margin-left: 0; }
        a:hover {
            color: #fff; } }

    &__info {
        min-height: 350px; }

    &__item-info {
        display: none; }
    &__wrap {
        display: inline-flex;
        @include r(1035) {
            flex-direction: column;
            align-items: center;
            width: 100%; } }

    &__img {
        width: 40%;
        justify-content: center;
        display: flex;
        align-items: center;
        @include r(1035) {
            width: 100%; }
        img {
            max-width: 480px;
            @include r(1035);
            width: 100%; } }

    &__description {
        width: 60%;
        color: #636363;
        font-size: 12pt;
        line-height: 27px;
        @include r(1035) {
            width: 100%; }
        padding: 0 15px;
        h3 {
            text-align: left;
            margin: 25px 0; } }

    &__items {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        p {
            font-size: 10px;
            text-align: center;
            padding-top: 10px;
            color: rgb(255,255,255);
            opacity: 0.5;
            @include r(1366) {
                padding-top: 0; } } }

    &__item {
        overflow: hidden;
        @include r(370) {
            width: 140px; }
        img {
            height: 80px;
            max-width: 170px;
            @include r(1366) {
                height: 60px; } }
        .image-block {
            height: 100px;
            width: 170px;
            transition: transform 350ms cubic-bezier(0.55, 0, 0.1, 1); }
        &:hover {
            cursor: pointer;
            transform: scale(1.35);
            transition: transform 350ms cubic-bezier(0.55, 0, 0.1, 1);
            @include r(1366) {
                transform: scale(1.15); } } } }


.image-block.our-products-image {
    height: 90px;
    @include r(1366) {
        height: 60px; } }

.arrow-container {
    position: relative;
    height: 60px;
    @include r(1366) {
        height: 45px; }
    @include r(1049) {
        height: 65px; }
    @include r(539) {
        height: 75px; }
    @include r(370) {
        height: 45px; } }
