.careers-text {
  padding: 25px 15px;


  &__wrap {
    position: relative; }

  &__title {
    text-transform: uppercase;
    line-height: 27px;
    font-size: 23px;
    margin-bottom: 40px; }

  &__image {
    position: absolute;
    right: 0;
    top: -80px;
    background-image: url(../img/reptaile-grey.png);
    background-repeat: no-repeat;
    background-position: center left;
    width: 532px;
    height: 377px;
    display: inline-block; }


  &__text {
    line-height: 27px;
    font-size: 20px;
    margin-bottom: 40px; } }

.careers {
  padding: 25px 0px;
  &__image {
    background-image: url(../img/position-bg.png);
    background-position: center;
    height: 300px;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover; }
  &__wrap {
    text-align: center;
    margin-bottom: 30px; } }

.fields-activity {
  padding: 25px 15px;

  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }

  &__image {
    min-height: 205px;
    max-height: 205px;
    display: flex;
    align-items: center;

    img {
      margin: 0 auto;
      max-width: 285px; } }

  &__activity {
    max-width: 285px;
    width: 100%;
    margin: 0 7px 15px;
    img,a {
      display: block; }

    a {
      width: 150px;
      border: 1px solid #B3874C;
      border-radius: 4px;
      text-align: center;
      height: 36px;
      line-height: 36px;
      margin: 50px auto 0;
      font-size: 13px;
      font-weight: bold;
      background-color: rgba(184, 150, 94, 0.2);
      &:hover {
        background-color: #B3874C;
        color: #fff;
        transition: color 170ms ease-in-out, background-color 170ms ease-in-out; } } } }
