.speaker-request {
  padding: 25px 15px; }

.speaker {
  max-width: 1000px;
  margin: 0 auto;
  &__block {
    display: flex;
    @include r(1035) {
      flex-direction: column;
      align-items: center; } }

  &__image {
    background-image: url(../img/speaker.png);
    background-size: cover;
    width: 50%;
    background-position: center;
    @include r(1035) {
      text-align: center;
      min-height: 350px;
      width: 100%; } }

  &__text {
    background-color: #326197;
    text-align: center;
    padding: 40px 0 60px 0;
    width: 50%;
    position: relative;
    @include r(1035) {
      width: 100%; }
    &:after {
      content: '';
      height: 2px;
      width: 184px;
      background-color: #B3874C;
      bottom: 50px;
      left: 50%;
      position: absolute;
      display: inline-block;
      transform: translateX(-50%);
      @include r(1035) {
        display: none; } }

    @include r(925) {
      height: auto; }

    p {
      color: #ffffff;
      line-height: 27px;
      font-size: 12pt;
      margin-bottom: 30px;
      padding: 0px 60px 0px;
      position: relative;
      text-overflow: ellipsis;
      overflow: hidden;
      &:before {
        content: '';
        position: absolute;
        right: 0;
        bottom: 0px;
        width: 20%;
        height: 20px; }
      &:last-child {
        margin-bottom: 0;
        font-style: italic;
        padding: 0px 35px 0px;
        @include r(1035) {
          height: auto; }
        &:before {
          content: '';
          position: absolute;
          right: 0;
          bottom: 4px;
          width: 20%;
          height: 20px;
          @include r(1035) {
            display: none; } }
        @include r(1035) {
          padding: 0px 35px; } } } }

  &__btn {
    text-align: center;
    margin-top: 30px; } }

.request-form {
	&__zip {
	  max-width: 25%; }
	&__country-code {
	  width: 65px; }
	&__wrap {
		input[type='tel'] {
			width: 200px; }
		input[type='text'] {
			width: 65px; }
  .form {
     &__input {
      background-color: rgba(255,255,255, 0.4); } } } }
