@include font(Avenir Next Regular, AvenirNext-Regular);
@include font(Avenir Next Bold, AvenirNext-Bold);
@include font(Avenir Next Medium, AvenirNext-Medium);

html, body {
  min-width: 320px;
  font-family: 'Avenir Next Regular', sans-serif;
  font-weight: 400;
  height: 100%; }

body {
    position: relative;
    padding-bottom: 125px; }

body:before {
    content: '';
    background-image: url(../img/site-background.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1; }

a {
  color: #B3874C;
  text-decoration: none; }

.container {
  max-width: $max-width;
  margin: 0 auto; }

.inputs-wrap {
  margin-bottom: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.small-line {
    width: 10px;
    height: 1px;
    background-color: #333333;
    display: inline-block;
    margin: 0 5px; }

.title-checkbox,
.range-text {
  font-size: 12pt;
  margin-left: 24px;
  color: #323232;
  line-height: 27px; }

.range-text {
  margin-left: 0; }

h1 {
  color: $text-color;
  margin: 0 0 50px;
  font-size: 36pt;
  font-family: 'Avenir Next Bold', sans-serif;
  text-align: center;
  font-weight: 400;
  line-height: 36pt; }


h2,.h2 {
  color: $text-color;
  font-size: 22pt;
  font-family: 'Avenir Next Medium', sans-serif;
  font-weight: 400;
  margin-bottom: 50px;
  text-align: center;
  line-height: 22pt; }

h3,.h3 {
  color: $text-color;
  font-size: 23px;
  text-align: center;
  margin-bottom: 50px;
  font-weight: 400;
  line-height: 23px; }

h4 {
  font-size: 17px;
  font-weight: 900;
  color: $text-color;
  margin-bottom: 13px;
  line-height: 27px; }


.button-xs {
    width: 50px;
    height: 36px;
    background-color: #B3874C;
    border: 1px solid #B3874C;
    border-radius: 4px;
    box-shadow: 0px 0px 5px 0px rgba(179, 135, 76, 0.16);
    color: #fff;
    font-size: 13px;
    font-weight: bold;
    text-transform: uppercase;
    transition: background-color 350ms cubic-bezier(0.55, 0, 0.1, 1);
    -webkit-appearance: none;
    &:hover {
      background-color: #cc9a57;
      transition: background-color 350ms cubic-bezier(0.55, 0, 0.1, 1); } }

.btn {
    height: 36px;
    line-height: 36px;
    background-color: #B3874C;
    color: $white;
    font-size: 13px;
    font-weight: bold;
    width: auto;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    border: 1px solid #B3874C;
    border-radius: 4px;
    box-shadow: 0px 0px 5px 0px rgba(179, 135, 76, 0.16);
    transition: background-color 350ms cubic-bezier(0.55, 0, 0.1, 1);
    padding: 0px 24px;
    margin: 0 auto;
    -webkit-appearance: none;
    &:hover {
      background-color: #cc9a57;
      transition: background-color 350ms cubic-bezier(0.55, 0, 0.1, 1); } }

p {
  // color: $text-color
  // font-size: 12pt
 }  // line-height: 27px

.line {
    display: block;
    width: 85%;
    margin: 0 auto;
    border-left: 1px solid #23583A;
    border-right: 1px solid #23583A;
    border-bottom: 1px solid #23583A;
    height: 27px;
    position: absolute;
    top: -8px;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
    transition: background-color 350ms cubic-bezier(0.55, 0, 0.1, 1); }

img {
  max-width: 100%;
  height: auto; }

input[type='checkbox'] + label {
  width: 20px;
  height: 20px;
  cursor: pointer;
  position: absolute;
  display: inline-block;
  left: 0;
  top: 2px;
  background-color: rgba(255,255,255,0.5);
  border-radius: 4px;
  border: 2px solid #B3874C;

  &:after {
    content: '';
    width: 13px;
    height: 7px;
    position: absolute;
    top: 2px;
    left: 2px;
    border: 2px solid #fff;
    border-top: none;
    border-right: none;
    background: transparent;
    opacity: 0;
    transform: rotate(-45deg);


    &:hover {
      opacity: 0.5; } } }

input[type="radio"] + label {
	width: 20px;
	height: 20px;
	cursor: pointer;
	position: absolute;
	display: inline-block;
	left: 0;
	background: #fff;
	border-radius: 4px;
	border: 2px solid #B3874C;

	&:after {
		content: '';
		width: 13px;
		height: 7px;
		position: absolute;
		top: 2px;
		left: 2px;
		border: 2px solid #fff;
		border-top: none;
		border-right: none;
		background: transparent;
		opacity: 0;
		transform: rotate(-45deg);


		&:hover {
			opacity: 0.5; } } }


input[type='checkbox'], input[type='radio'] {
  visibility: hidden;
  &:checked + label {
    background-color: #B3874C; }
  &:checked + label:after {
    opacity: 1; } }


input[type='checkbox'] {
  display:none {} }

input[type='range'] {
  width: 210px;
  height: 20px;
  overflow: hidden;
  cursor: pointer;
  outline: none; }


input[type='range'],
input[type='range']::-webkit-slider-runnable-track,
input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none;
  background: none; }


input[type='range']::-webkit-slider-runnable-track {
  width: 200px;
  height: 1px;
  background: #1851B6; }


input[type='range']:nth-child(2)::-webkit-slider-runnable-track {
  background: none; }


input[type='range']::-webkit-slider-thumb {
  position: relative;
  height: 20px;
  width: 20px;
  margin-top: -10px;
  background: #fff;
  border: 1px solid #1851B6;
  border-radius: 4px;
  z-index: 1;
  background-color: #1851B6; }


input[type='range']:nth-child(1)::-webkit-slider-thumb {
  z-index: 2; }

