.product-preview {
  background-color: #ffffff;
  cursor: pointer;
  max-width: 285px;
  width: 100%;
  position: relative;
  margin: 0px 1% 15px;
  margin-bottom: 15px;
  &__price {
    span {
      color: #404040;
      &:last-child {
        color: #2A6B46;
        font-weight: bold; } } }
  &__text {
    border-top: 4px solid #E8E8E8;
    padding: 12px 20px;
    z-index: 10; }
  &__title {
    color: #404040;
    font-size: 12pt;
    line-height: 21px; }
  &__description {
    font-size: 14px;
    color: #A1A1A1;
    line-height: 21px; }
  &--single {
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.16);
    transition: box-shadow 350ms cubic-bezier(0.55, 0, 0.1, 1);
    @include r(1250) {
      max-width: 40%; }
    @include r(925) {
      max-width: 60%; }
    @include r(768) {
      max-width: 310px; }
    &:hover {
      box-shadow: 0px 0px 20px 0px rgba(0,0,0, .27);
      transition: box-shadow 350ms cubic-bezier(0.55, 0, 0.1, 1);
      margin: 0px 1% 100px;
      min-height: initial !important;
      .hover-block {
        display: block; }
      .product-preview__text {
        position: absolute;
        background: #fff;
        width: 100%;
        transition: box-shadow 350ms cubic-bezier(0.55, 0, 0.1, 1);
        box-shadow: 0px 6px 20px 0px rgba(0,0,0, .27); }
      .image-block {
        img {
          transform: scale(1.1);
          transition: transform 350ms cubic-bezier(0.55, 0, 0.1, 1); } } } }

  &--related {
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.16);
    &:hover {
      .product-preview__text {
        position: relative;
        box-shadow: initial; } }
    @include r(1250) {
      max-width: 100%; }
    @include r(925) {
      max-width: 100%; }
    @include r(635) {
      max-width: 285px; } }

  &--small {
    max-width: 180px;
    width: 100%;
    margin: 0 10px;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.16);
    @include r(1250) {
      max-width: 180px;
      margin: 0 15px 20px; }
    @include r(925) {
      max-width: 180px; }

    @include r(768) {
      max-width: 180px;
      margin: 0 15px 20px;
      &:hover {
        margin: 0 15px 20px; } }
    &:hover {
      @include r(1200) {
        margin: 0 15px 20px; }
      @include r(925) {
        margin: 0 15px 20px; }
      .image-block {
        img {
          transform: initial; } }
      .product-preview__text {
        box-shadow: initial;
        transform: initial;
        position: initial; } }

    .product-preview__title {
      color: #404040;
      font-size: 12px;
      text-align: center; }

    .image-block {
      height: auto; } }




  &--posts {
    display: none;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.16);
    float: left;
    margin: 0 0.5% 20px;
    @include r(1250) {
      max-width: 280px; }
    @include r(1235) {
      //width: 23%
      margin: 0 0.5% 20px; }
    @include r(992) {
      //width: auto
      max-width: 310px; }
    @include r(925) {
      //width: auto
      max-width: 310px;
      margin: 0 0.5% 20px;

      &:hover {
        margin: 0 0.5% 20px; } }

    @include r(768) {
      margin: 0 .5% 20px;

      &:hover {} }

    &:hover {
      box-shadow: 0px 0.5px 20px 0px rgba(0,0,0, .27);
      transition: box-shadow 350ms cubic-bezier(0.55, 0, 0.1, 1);

      @include r(1200) {
        margin: 0 0.5% 20px; }
      .image-block {
        img {
          transform: scale(1.1);
          transition: transform 350ms cubic-bezier(0.55, 0, 0.1, 1); } }
      .product-preview__text {
        box-shadow: initial;
        transform: initial;
        position: initial; } } } }


