.software-services {
    &__plugins-btn {
        display: flex;
        justify-content: center;
        a {
            margin: 0;
            margin-top: 30px; } }
    &__plugins {
        max-width: 530px;
        width: 100%;
        height: 100%;
        padding: 0 10px; }
    &__plugins:not(:last-child) {
        @include r(1050) {
            padding-bottom: 30px; } }
    &__block {
        display: flex;
        justify-content: space-between;
        @include r(1050) {
            flex-direction: column;
            align-items: center; } } }

