.compacts {
  .features__item {
    max-height: initial;
    @include r(1025) {
      flex-direction: column;
      align-items: center; } } }

.application__text {
  text-align: left;
  -ms-flex-align: center;
  align-items: center;
  padding: 40px 72px;
  position: relative;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  @include r(767) {
    padding: 0!important; }

  p {
    max-height: 185px;
    position: relative;
    @include r(925) {
      max-height: initial; }
    &:after {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      width: 20%;
      height: 20px;
      @include r(925) {
        display: none; } } } }


.application__btn {
  width: auto;
  height: 36px;
  background-color: #b3874c;
  border: 1px solid #b3874c;
  border-radius: 4px;
  box-shadow: 0 0 5px 0 rgba(0,0,255,.16);
  color: #fff;
  font-size: 13px;
  font-weight: 700;
  padding: 10px;
  @include r(635) {
    width: 100%;
    margin: 5px; } }

.application__title {
	text-align: left;
	margin: 0 0 25px; }

.application-text_text {
  margin-bottom: 30px;
  &:last-child {
    margin-bottom: 0; } }

#application-content {
    max-height: none;
    text-overflow: unset;
    overflow: unset;
    p {
      max-height: 100%; } }

#application-text {
  padding-top: 10px; }

#features-item {
  max-height: none; }

.ellipsis {
	color: #14281D; }

.more {
    color: #b3874c;
    text-decoration: none;
    display: none;
    font-weight: bold;
    padding: 0;
    cursor: pointer; }

.less {
    display: none; }

.fa {
	  margin: 0 5px; }

.text-readmore {
    max-height: 165px;
    overflow: hidden; }

#full-text {
    max-height: none;
    height: auto; }

#application-text {
  .text-readmore {
    max-height: 165px;
    overflow: hidden; } }

#application-text {
  #full-text {
    max-height: none;
    height: auto; } }

.app-action-buttons {
  margin-top: 10px; }

.modal-testimonials-img {
  padding: 10px; }
