.reviews {
    padding: 0 15px;
    &__block {
        display: flex;
        justify-content: space-between;
        align-items: start; }

    &__review {
        max-width: 290px; }

    &__video {
        iframe {
            max-width: 100%; } }

    .software {
        &__title {
            font-size: 12pt;
            color: $text-color;
            font-weight: 600;
            margin-bottom: 20px;
            text-align: left;
            padding: 0 5px; }

        &__subtitle {
            padding: 0 5px; }

        &__list {
            margin-top: 20px; }


        &__item {
            color: $text-color;
            font-size: 12pt;
            line-height: 27px; }

        &__link {
            font-size: 12pt;
            line-height: 27px; }
        &__button {
            text-align: center; } }
    .review {
        &__text {
            font-size: 12pt;
            line-height: 27px;
            color: $text-color;
            height: 215px;
            overflow: hidden; }
        &__wrap {
            font-size: 14px;
            color: $text-color;
            text-align: left;
            padding: 20px;
            box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.16);
            position: relative;
            background-color: #fff;
            &:after {
                content: '';
                position: absolute;
                display: inline-block;
                width: 40px;
                height: 25px;
                left: 50%;
                bottom: -25px;
                -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
                background-image: url(../img/tringle.png); }
            &:before {
                content: '';
                position: absolute;
                right: 25px;
                bottom: 25px;
                width: 20%;
                height: 20px; } }

        &__name {
            font-size: 12pt;
            color: #626466;
            line-height: 27px;
            text-align: center;
            margin-top: 25px; }

        &__position {
            color: #95989a;
            font-size: 14px;
            line-height: 19px;
            text-align: center; } } }
.reviews__software {
    background-color: #fff;
    border-radius: 10px;
    padding: 10px;
    box-shadow: 0 0 7px 0 rgba(0,0,0,.16);
    max-width: 290px;
    @include r(768) {
        max-width: 80%; } }
.software__text {
    font-size: 12pt;
    line-height: 27px;
    color: $text-color; }
