.contact-us {
  padding: 25px 15px;
  .container {
    max-width: 1000px; }
  &__block {
    display: flex;
    margin-bottom: 50px;
    height: 280px;
    @include r(925) {
      flex-direction: column;
      height: auto; }

    &:last-child {
      margin-bottom: 0; }

    &:nth-child(odd) {
      .contact-us__map {
        order: 2;
        @include r(925) {
          order: initial; } } } }
  &__map {
    width: 100%;
    max-width: 500px;
    @include r(925) {
      text-align: center;
      max-width: initial; }

    img {
      height: 100%;
      width: 100%;
      @include r(925) {
        margin-bottom: 30px;
        width: auto; } } }
  &__text {
    background-color: #326197;
    text-align: center;
    width: 100%;
    padding: 40px 50px;
    position: relative;
    max-width: 500px;
    @include r(925) {
      max-width: initial; }
    &:after {
      content: '';
      position: absolute;
      height: 1px;
      width: 184px;
      background-color: #B3874C;
      left: 50%;
      transform: translateX(-50%);
      bottom: 40px; }
    h4 {
      font-size: 27px;
      color: #ffffff;
      line-height: 27px;
      font-weight: 400; }
    p {
      font-size: 17px;
      color: #ffffff;
      line-height: 27px;
      &:last-child {
        margin-top: 30px; } } } }

.helps {
  padding: 25px 15px;
  &__list {
    display: flex;
    justify-content: center;
    @include r(925) {
      flex-wrap: wrap; } }
  &__item {
    background-color: rgba(184, 150, 94, 0.2);
    height: 36px;
    display: flex;
    align-items: center;
    border: 1px solid #B3874C;
    border-radius: 4px;
    padding: 0 25px;
    margin-right: 50px;
    transition: background 250ms cubic-bezier(0.55, 0, 0.1, 1);
    text-align: center;
    line-height: 14px;
    @include r(925) {
      margin: 0 10px 10px; }
    @include r(576) {
      width: 100%;
      text-align: center;
      justify-content: center;
      margin: 0 auto 10px; }


    &:last-child {
      margin-right: 0; }
    &:hover {
      background-color: #B3874C;
      transition: background 250ms cubic-bezier(0.55, 0, 0.1, 1);
      a {
        color: #fff; } }

    a {
      font-size: 13px;
      font-weight: 900;
      text-align: center; } } }
