@import "helpers";
@import "fonts";

.goods {
    width: 100%;
    padding: 25px 15px;
    background-color: #F9F9F9;
    &__wrapper {
        max-width: $max-width;
        margin: 0 auto; }
    &__buttons {
        position: relative;
        text-align: center;
        margin-top: 33px;
        z-index: 9; }

    &__btn {
        max-width: 210px; }


    &__items {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        width: 100%; }

    &__item {
        width: 180px;
        box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.15);

        img {
            display: block;
            margin: 15px auto; }

        span {
            text-align: center;
            display: block;
            border-top: 3px solid #E8E8E8;
            font-size: 12px;
            color: #404040;
            padding: 10px 0; } } }
