.features {
    width: 100%;
    padding: 25px 15px;

    .container {
        max-width: $max-width-sm; }

    &__customers-title {
        font-size: 28px;
        text-align: center;
        color: $title-color;
        font-family: 'Avenir Next Regular', sans-serif;
        font-weight: 400; }

    &__items {}

    &__image {
        background-image: url(../img/vnas-image.png);
        background-position: center;
        width: 40%;
        background-size: cover;
        @include r(1035) {
            width: 100%;
            min-height: 235px; } }

    &__item {
        display: flex;
        align-items: stretch;
        margin-bottom: 50px;
        overflow: hidden;
        @include r(1035) {
            flex-direction: column;
            align-items: center;
            max-height: none; }
        &:last-child {
            margin-bottom: 0; }
        &:nth-child(even) {
            .features__text {
                order: 2; } }
        img {
            max-width: 450px;
            @include r(1035) {
                width: 100%;
                margin: 0 auto 30px; }
            @include r(992) {
                max-width: 450px; }
            @include r(767) {
                max-width: 450px!important; } }
        &__img-container {
            background-image: url(../img/lizard.jpg);
            background-size: cover;
            width: 50%;
            background-position: center;
            @include r(1035) {
                width: 100%;
                min-height: 350px; } } }

    &__wrap {
        display: flex;
        align-items: stretch; }

    &__text {
        background-color: #2D467A;
        text-align: center;
        display: flex;
        align-items: center;
        padding: 20px 32px 60px 32px;
        position: relative;
        flex-direction: column;
        justify-content: center;
        width: 60%;
        &:after {
            content: '';
            position: absolute;
            height: 2px;
            width: 184px;
            background-color: #B3874C;
            border-radius: 7px;
            left: 50%;
            transform: translateX(-50%);
            bottom: 40px;
            @include r(1035) {
                display: none; } }
        @include r(1035) {
            order: 2;
            width: 100%; }
        p {
            font-size: 12pt;
            line-height: 27px;
            color: #fff;
            word-wrap: break-word;
            position: relative;
            width: 100%;
            @include r(1035) {
                max-height: initial; }
            &:last-child {
                position: relative;
                //padding-bottom: 40px
                @include r(925) {
                    padding-bottom: 0; } } }
        a {
            margin-top: 10px; } }

    &__line {
        position: absolute;
        height: 2px;
        width: 184px;
        background-color: #B3874C;
        border-radius: 7px;
        left: 50%;
        transform: translateX(-50%);
        bottom: 35px; }

    &__btn {
        text-align: center;
        margin-top: 47px; } }

.vnas {
    .container {
        max-width: 1000px;
        margin: 0 auto; }

    &__items {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        margin-bottom: 47px; }

    &__item {
        display: flex;
        flex: 1 1 20%;
        padding-top: 30px;
        flex-direction: column;
        width: 98px;
        text-align: center;
        align-items: center;
        @include r(795) {
             flex: 1 1 30%; }

        img {
            max-width: 100%;
            height: 70px; } }

    &__text {
        font-size: 12pt;
        color: $text-color;
        line-height: 27px;
        margin-top: 30px;
        @include rhrwl(750,800) {
            word-wrap: break-word;
            word-break: break-word; } }

    &__btn {
        text-align: center; } }
