.lists-text {
  margin: 20px 0 20px 50px;
  & > li {
    list-style-type: disc;
    color: $text-color;
    font-size: 12pt;
    line-height: 27px; } }

.features__btn {
  &--vnas {
    margin: 50px auto 0;

    & > a {
      width: auto;
      padding: 0 10px; } } }

.accardion--vna-solution {
  .accardion {
    &__unit {
      width: 100%;
      padding: 30px;
      &:first-child {
        border-right: none;
        border-bottom: none; }
      p {
        text-align: justify; } }

    &__description {
      @include r(850) {
        min-height: 350px;
        height: auto;
        display: flex;
        flex-direction: column;
        justify-content: center; } } } }
