.form--faqs {
  max-width: 350px;
  margin-bottom: 30px;

  input {
    margin: 0;
    border: none;
    border-radius: 0;
    &:focus {
      border: none; } } }

.accardion-collapse {
  overflow: hidden;
  background-color: rgba(255,255,255,0.5);
  &__inside {
    border: none;
    color: #636363;
    font-size: 12pt;
    position: relative;
    height: 0;
    opacity: 0;
    overflow: hidden;
    padding: 0;
    transition: opacity 0s step-star, height 1s step-start, padding 0s step-start;
    //top: -500px
    // overflow: hidden
 } }    //transition: padding 0ms cubic-bezier(0.55, 0, 0.1, 1), height 0ms cubic-bezier(0.55, 0, 0.1, 1),opacity 350ms cubic-bezier(0.55, 0, 0.1, 1)

.active-accardion-content {
  opacity: 1;
  height: auto;
  padding: 30px;
  transition: opacity 0.5s linear,height 0s step-start,padding 0s step-start; }
