.leadership {
    text-align: center;
    .container {
      max-width: 1000px;
      position: relative;
      @include r(1200) {
        .testimonials__arrow {
          top: 50%; }
        .arrow-left {
          left: 0; }
        .arrow-right {
          right: 0; } } }

    .slick-list, .slick-list > .slick-track {
      width: 100%;
      .slick-slide {
        margin: 0 15px;
        .image-block {
          flex-direction: column; } } }

    h2, h3 {
      text-align: left; }
    .product-preview__text {
      border: 0; } }

.about-text {
  padding: 25px 15px;
  background-image: url(../img/text-bg-about.png);
  background-position: center center;
  background-repeat: no-repeat;
  h2, h3 {
    text-align: left; }

  .container {
    max-width: 1000px; }

  &__block {
    display: flex;
    justify-content: space-between;

    & > div {
      @include r(935) {
        margin-right: 25px; } } } }

.features--about {
  .container {
    max-width: 1000px; }

  .features {
    &__item {
      max-height: none;
      p {
        max-height: none; }
      @include r(1030) {
        height: initial; } }

    &__text {
      display: block;
      padding: 40px 60px 70px 60px; }

    &__wrap {
      @include r(1030) {
        display: block; } }

    .testimonials {
      &__items {
        align-items: center;
        justify-content: space-around; }

      &__arrow {
        top: 100px; } } } }
.about-us-text {
  width: 50%;
  @include r(1035) {
    width: 100%; } }

.leadership-block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start; }
