.breadcrumbs {
    width: 100%;
    margin-bottom: 50px;


    &__list {
        display: flex;
        list-style-type: none;
        align-items: baseline;
        flex-wrap: wrap; }

    &__item,
    &__separator {
        font-size: 28px;
        color: $title-color; }

    &__separator {
        margin: 0 10px; }

    &__link {
        font-size: 28px; } }
