.technical-library {
  padding: 25px 0;
  img {
    width: 100%; }

  &__images {
    background-image: url(../img/technical-library.png);
    height: 274px;
    background-size: cover;
    background-position: center 0; } }

.form--posts {
  max-width: 350px;
  margin-bottom: 30px;

  input {
    margin: 0;
    border: none;
    border-radius: 0;
    &:focus {
      border: none; } } }

.posts {
  &__products {
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    @include r(1200) {
      justify-content: center; }

    .hover-block {
      text-align: center;
      &__text {
        text-align: left; } } }

  &__button {
    text-align: center;
    margin-top: 30px;

    a {
      background-color: rgba(184, 150, 94, 0.2); } } }
