.plugins {
  &__text {
    p {
      margin-bottom: 30px; }

    ul {
      li {
        margin-bottom: 5px;
        line-height: 27px;
        font-size: 12pt;
        color: $text-color; } } } }
.license {
  &__text {
    p {
      margin-bottom: 30px;
      &:last-child {
        font-style: italic;
        text-transform: uppercase;
        margin-bottom: 0; } } } }

.table {
  box-shadow: 0px 0px 7px 0px rgba(0,0,0, .16);
  thead {
    tr {
      height: 100px;
      background-color: #326197;
      th {
        vertical-align: middle;
        text-align: left;
        color: #FFFFFF;
        font-size: 21px;
        line-height: 21px;
        font-weight: 900;
        &:first-child {
          padding-left: 20px; } } } }
  tbody {
    tr {
      &:nth-child(even) {
        background-color: #F3F3F3; }
      &:nth-child(odd) {
        background-color: #ffffff; }
      td {
        font-size: 12pt;
        color: #636363;
        vertical-align: middle;
        padding: 15px 0;
        &:first-child {
          padding-left: 20px; }
        span {
          display: block;
          margin-bottom: 10px;
          &:last-child {
            margin-bottom: 0; } } } } } }
