@import "helpers";
@import "fonts";

.top {
    width: 100%;
    background: url(../img/bg-line-full.png) no-repeat bottom center, linear-gradient(to right, #153623 ,#2A6B46);
    height: 100vh;
    position: relative;
    margin-top: 0px;
    @include rharw(1050, 500) {
        height: 140vh; }
    @include rharw(586, 500) {
        height: 165vh; }
    @include rharw(586, 340) {
        height: 220vh; }
    @include rharw(820, 440) {
        height: 170vh; }
    @include rharw(370, 640) {
        height: 130vh; }
    @include rharw(415, 800) {
        height: 120vh; }
    @include rharw(415, 735) {
        height: 100vh; }
    @include rharw(320, 570) {
        height: 145vh; }
    @include rharw(375, 670) {
        height: 120vh; }
    @include rharw(415, 740) {
        height: 100vh; }
    @include rharw(360, 640) {
        height: 135vh; }

    overflow: hidden;
    margin-top: -90px;
    &__wrapper {
        max-width: $max-width;
        margin: 0 auto; }

    &__title {
        font-size: 48px;
        color: $white;
        text-align: center;
        margin-top: 100px;
        margin-bottom: 10px;
        @include rharw(1400, 900) {
            margin-top: 70px; } }

    &__span {
        display: block;
        color: #ffffff;
        font-weight: 300;
        font-size: 38px;
        line-height: 38px;
        @include rharw(1400, 900) {
            font-size: 25px; }
        @include rharw(415,800) {
            font-size: 20px; }
        @include r(400) {
            line-height: 45px;
            margin-bottom: 15px; } }

    &__block {
        display: flex;
        flex-direction: column;
        align-items: center; }

    &__text {
        font-size: 14px;
        line-height: 27px;
        color: $white;
        text-align: center;
        position: relative;
        z-index: 2;
        @include r(625) {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            width: 90%;
            font-size: 12pt;
            line-height: 35px; } }

    &__image {
        position: relative;
        z-index: 1;
        max-width: 100%; } }

.header {
    height: 90px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-attachment: fixed;
    left: 0;
    width: 100%;
    z-index: 99;
    transform: translateZ(0);

    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 90px;
        padding: 0 15px;
        position: relative; }
    &__logo {
        margin-left: auto; }
    &__nav {
        margin-left: auto; }
    .nav__list {
        .mobile-sub-menu {
            transform: translate3d(320px, 0px, 0px);
            left: 0px;
            padding-bottom: 50px;
            transition: transform 350ms cubic-bezier(0.55, 0, 0.1, 1), display 350ms cubic-bezier(0.55, 0, 0.1, 1);
            opacity: 1;
            visibility: visible;
            display: block; } }

    .is-visible {
        left: 0px;
        transition: left 350ms cubic-bezier(.55,0,.1,1);
        overflow: scroll;
        overflow-x: hidden;
        @include scrollbar() {
            background-color: #F3F3F3;
            width: 5px; }
        @include scrollbar-track() {
            width: 5px;
            background-color: #F3F3F3; }
        @include scrollbar-thumb() {
            width: 5px;
            height: 20px;
            background-color: #326197; } }


    .back-to-menu {
        position: absolute;
        display: none;
        border: 1px solid #B3874C;
        background: transparent;
        padding: 5px 10px;
        top: 20px;
        left: 30px;
        border-radius: 10px;
        color: #B3874C;
        text-transform: uppercase;
        transition: border 350ms cubic-bezier(.55,0,.1,1), color 350ms cubic-bezier(.55,0,.1,1), background 350ms cubic-bezier(.55,0,.1,1);
        &:hover {
            background: #B3874C;
            color: #ffffff;
            border: 1px solid #B3874C; } }

    .show-btn {
        display: block;
        z-index: 9999; } }


.arrow-bottom {
    background-image: url(../img/arrow-bottom.png);
    height: 11px;
    width: 20px;
    position: absolute;
    bottom: 25px;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
    z-index: 9;
    animation-name: arrow-jumping;
    animation-iteration-count: infinite;
    animation-duration: 0.7s; }



.nav {
    display: flex;
    align-items: center;
    transition: 0.55s;


    &__list {
        display: flex; }

    &__item {
        position: relative;
        display: flex;
        align-items: center;
        margin-right: 30px;
        &:last-child {
            margin-right: 45px;
            ul {
                @include r(1365) {
                    right: 0;
                    left: initial; } } }
        &:hover {
            & > a {
                text-shadow: 0 0 .9px #B3874C;
                color: #B3874C;
                transition: text-shadow 170ms ease-in-out, background-color 170ms ease-in-out; }
            ul {
                visibility: visible;
                opacity: 1; } }
        & > a {
            font-size: 13px;
            text-transform: uppercase;
            color: $white;
            text-decoration: none;
            transition: border 170ms ease-in-out, background-color 170ms ease-in-out;
            &.active-menu {
                text-shadow: 0 0 .9px #B3874C;
                color: #B3874C; } }

        & > ul {
            position: absolute;
            list-style-type: none;
            background: rgba(179,135,76, .9);
            padding: 20px;
            top: 30px;
            left: 0px;
            margin-left: -.1em;
            white-space: nowrap;
            text-align: left;
            text-transform: uppercase;
            border-radius: 4px;
            visibility: hidden;
            opacity: 0;
            transition: visibility 350ms cubic-bezier(0.55, 0, 0.1, 1), opacity 350ms cubic-bezier(0.55, 0, 0.1, 1), transform 350ms cubic-bezier(0.55, 0, 0.1, 1), display 350ms cubic-bezier(0.55, 0, 0.1, 1);
            z-index: 99999;
            // +r(1090)
            //     &:after
            //         content: 'Back'
            //         display: inline-block
            //         cursor: pointer
            //         position: absolute
            //         top: -40px
            //         left: 20px
            //         height: 30px
            //         line-height: 30px
            //         border: 1px solid #B3874C
            //         background: transparent
            //         padding: 0px 15px
            //         border-radius: 10px
            //         color: #B3874C
            //         text-transform: uppercase
            //         transition: border 350ms cubic-bezier(.55,0,.1,1), color 350ms cubic-bezier(.55,0,.1,1), background 350ms cubic-bezier(.55,0,.1,1)
            //         &:hover
            //             background: #B3874C
            //             color: #ffffff
            //             border: 1px solid #B3874C

            & > li {
                background-color: transparent;
                & > a {
                    display: block;
                    padding: .382em;
                    color: rgba(255,255,255, .7);
                    text-decoration: none;
                    text-transform: uppercase;
                    font-size: 13px;
                    transition: text-shadow 170ms ease-in-out, background-color 170ms ease-in-out;
                    &.active-sub-menu {
                        @include rmin(1090) {
                            text-shadow: 0 0 .9px #B3874C;
                            color: rgba(255,255,255,1); }
                        @include r(1090) {
                            text-shadow: 0 0 .9px #B3874C;
                            color: #B3874C; } }
                    &:hover {
                        text-shadow: 0 0 .9px #B3874C;
                        color: rgba(255,255,255,1);
                        transition: text-shadow 170ms ease-in-out, background-color 170ms ease-in-out; } } } } }



    &__search {
        position: absolute;
        right: 15px;
        top: 28%; } }



.animate-strings {
    font-weight: bold;
    color: #B3874C;
    display: inline-block;
    height: 38px; }
.with-sub-menu {
    & > a {
        &:after {
            content: '';
            display: inline-block;
            background-image: url(../img/menu-arrow-small.png);
            width: 10px;
            height: 6px;
            margin-left: 11px;
            position: relative;
            top: -1px; } }

    &:hover {
        & > a {
            &:after {
                background-image: url(../img/arrow-hover-menu.png); } } } }

.with-sub-menu-second-level {
    display: inline-block;
    margin-right: 0;
    width: 100%;
    & > a {
        &:after {
            content: '';
            display: inline-block;
            background-image: url(../img/menu-arrow-small.png);
            width: 10px;
            height: 6px;
            margin-left: 11px;
            position: relative;
            top: -1px; } }
    &:hover {
        & > a {
            color: rgba(255,255,255,.7);
            &:after {
                -webkit-transform: rotate(180deg);
                -moz-transform: rotate(180deg);
                -ms-transform: rotate(180deg);
                -o-transform: rotate(180deg);
                transform: rotate(180deg); } }
        ul.sub-nav__list, ul.sub-menu {
            visibility: visible;
            display: block;
            opacity: 1;
            position: relative; } }
    ul.sub-nav__list, ul.sub-menu {
        visibility: hidden;
        display: none;
        opacity: 0;
        position: relative;
        top: 0;
        background: transparent;
        padding: 0 0 0 20px;
 } }        // border: 1px solid #fff

.website-search-page {
    display: flex;
    width: 0;
    box-sizing: border-box;
    border-radius: 4px;
    font-size: 16px;
    height: 40px;
    -webkit-transition: width 0.5s ease-in-out;
    -moz-transition: all .5s ease;
    transition: all .5s ease;
    cursor: pointer;
    background-color: #ffffff; }

.website-search-page-focus {
    width: 350px;
    padding: 10px;
    background-color: #ffffff; }

.search-header-btn:hover {
    cursor: pointer; }

.search-header-btn {
    float: right;
    margin-top: 10px;
    padding-left: 10px; }

.search-page-form {
    float: left; }

.serch-page-form {
    text-align: center;
    margin-bottom: 10px;
    font-size: 20px; }

.search-results {
    text-align: center;
    color: #636363; }

.search-page-title {
    color: #636363;
    margin: 0; }

.search-item {
    margin-bottom: 15px;
    transition: box-shadow 350ms cubic-bezier(.55,0,.1,1);
    background-color: rgba(255,255,255,0.6);
    border: 1px solid #e6e6e6;
    padding: 10px;
    min-height: 76px; }

#website-search-page {
    width: 300px;
    height: 36px;
    border-bottom: 1px solid #e6e6e6;
    padding: 0 10px 0 10px; }

.not-found-results {
    font-size: 25px;
    text-align: center; }

@keyframes arrow-jumping {
    from {
        bottom : 25px; }
    to {
        bottom : 35px; } }

.nav__list li {
    &:hover {
        cursor: pointer; } }

.without-sub-menu {
    & > a {
        padding: 9px 0; } }
