.testimonials {
    width: 100%;
    padding: 25px 15px;

    .container {
        position: relative;
        max-width: 1050px;
        margin: 0 auto; }

    &__wrap {
        max-width: 1050px;
        margin: 0 auto;
        position: relative; }

    &__items {
        display: flex;
        justify-content: space-between; }

    &__item {
        padding: 10px 30px 0;

        img {
            margin: 0 auto;
            height: 70px; } }

    &__wrap {
        background-color: #fff;
        box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.16);
        padding: 20px;
        margin-bottom: 70px;
        position: relative;
        @include r(768) {
            max-width: 350px; }
        &:after {
            content: '';
            position: absolute;
            display: inline-block;
            width: 40px;
            height: 25px;
            left: 50%;
            bottom: -25px;
            transform: translateX(-50%);
            background-image: url(../img/tringle.png); } }
    p {
        position: relative;
        text-overflow: ellipsis;
        overflow: hidden;
        &:after {
            opacity: 0.1;
            content: '';
            position: absolute;
            right: 0;
            bottom: 0;
            width: 20%;
            height: 20px;
            background-image: linear-gradient(to right, rgba(255,255,255,0), rgba(255,255,255,1)); } }


    &__text {
        color: $text-color;
        font-size: 14px;
        line-height: 21px;
        height: 170px;
        overflow: hidden;
        @include r(435) {
            height: 245px; }
        p {
            line-height: 21px;
            font-size: 14px; } }
    &__author {
        font-size: 19px;
        color: $text-color;
        margin-top: 20px;
        margin-bottom: 8px; }
    &__position {
        color: $text-color;
        font-size: 14px; }

    &__arrow {
        width: 19px;
        height: 34px;
        display: inline-block;
        top: 195px;
        position: absolute;
        cursor: pointer;
        background-repeat: no-repeat; }

    .arrow-left {
        background-image: url(../img/arrow-slider-left.png);
        left: -55px; }


    .arrow-right {
        background-image: url(../img/arrow-slider-right.png);
        right: -55px; } }

