.representative {
  padding: 25px 15px;

  &__text {
    color: #636363;
    font-size: 12pt;
    line-height: 27px; } }

.map-container {
  position: relative;
  display: flex;
  min-height: 570px;
  box-shadow: 0px 0px 7px rgba(0,0,0, .16);
  margin-top: 50px;
  background-color: rgba(255,255,255,.6);
  @include r(925) {
    flex-direction: column;
    box-shadow: initial;
    background-color: transparent; }

  &__description {
    margin-top: 50px;
    min-width: 350px;
    max-height: 570px;
    overflow-y: scroll;
    overflow-x: hidden;
    scrollbar-face-color: #b46868;
    background-color: transparent;
    @include r(925) {
      width: 100%;
      min-width: initial;
      max-height: 500px;
      margin: 50px auto 50px;
      box-shadow: 0 0 7px rgba(0,0,0,.16);
      background-color: rgba(255, 255, 255, 0.6); } }

  &__search {
    border-bottom: 1px solid #E6E6E6;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      right: 20px;
      top: 50%;
      display: inline-block;
      width: 20px;
      height: 20px;
      transform: translateY(-50%);
      background-image: url(../img/search-icon.png); }
    input {
      height: 50px;
      line-height: 50;
      padding: 0 20px;
      font-size: 13px;
      font-weight: bold;
      color: #636363;
      width: 100%;
      position: relative; } }

  &__item {
    border-bottom: 1px solid #E6E6E6;
    padding: 20px;
    cursor: pointer;
    transition: background-color 350ms cubic-bezier(0.55, 0, 0.1, 1);
    &:last-child {
      border-bottom: none; }

    &:hover {
      background-color: #B3874C;
      transition: background-color 350ms cubic-bezier(0.55, 0, 0.1, 1);
      p, a {
       color: #fff;
       transition: color 350ms cubic-bezier(0.55, 0, 0.1, 1); } } }

  &__item-title {
    font-size: 21px;
    line-height: 20px;
    margin-bottom: 22px; }

  &__item-name {
    font-size: 16px;
    font-style: italic;
    line-height: 21px; }

  &__item-email,&__item-phone,&__item-site {
    font-size: 14px;
    line-height: 21px;
    a {
      color: #636363;
      font-weight: 900; } }

  &__map {
    background-image: url(../img/map-bg.png);
    background-repeat: no-repeat;
    width: 100%;
    min-height: 570px;
    background-size: cover;
    @include r(925) {
      min-height: 450px; } }

  &__wrap {
    position: relative; } }


.map-container__description::-webkit-scrollbar {
  background-color: #F3F3F3;
  width: 2px; }


.map-container__description::-webkit-scrollbar-track {
  width: 2px;
  background-color: #F3F3F3; }


.map-container__description::-webkit-scrollbar-thumb {
  width: 2px;
  height: 20px;
  background-color: #326197; }

.location-selector {
  display: flex;
  @include r(925) {
    flex-direction: column; }

  &_wrap {
    display: flex;
    flex-direction: column;
    width: 100%; }

  &_region,&_state {
    display: flex;
    width: 100%;
    box-shadow: 0 0 7px rgba(0,0,0,.16);
    border: 0;
    height: 100%;
    margin-top: 10px;
    background-color: rgba(255,255,255,.6);
    max-height: 108px;
    min-height: 80px;
    flex-direction: column;
    overflow-y: auto;
    @include r(925) {
      width: 100%;
      margin: 10px 0 10px 0; } }

  &_region {
    flex-direction: row; }

  &_title-region,&_title-state {
    display: flex;
    font-weight: 600;
    color: #636363;
    font-size: 12pt;
    line-height: 27px; }

  &_region-item {
    color: #636363;
    font-size: 12pt;
    line-height: 27px;
    padding: 0 15px;
    min-height: 27px;
    &:hover {
      cursor: pointer;
      background: #b3874c;
      color: #ffffff; } }

  &_state::-webkit-scrollbar-track,&_region::-webkit-scrollbar-track,&_state::-webkit-scrollbar,&_region::-webkit-scrollbar {
    width: 2px;
    background-color: #f3f3f3; }

  &_state::-webkit-scrollbar-thumb,&_region::-webkit-scrollbar-thumb {
    width: 2px;
    height: 20px;
    background-color: #326197; }

  &_arrow-wrap {
    background: url(../img/location-arrow.svg) no-repeat center 65%;
    width: 150px;
    @include r(925) {
      width: 60px;
      height: 60px;
      margin: 0 auto;
      -webkit-transform: rotate(90deg);
      transform: rotate(90deg); } }

  &_first-column,&_second-column {
    display: flex;
    flex-direction: column;
    width: 50%; } }


.selected-region, .selected-state {
  background: #b3874c;
  color: #ffffff; }





