@import "helpers";

.footer {
    width: 100%;
    padding: 0;
    clear: both;

    .container {
        position: relative;
        height: 100%;
        margin: 0;
        max-width: 100%;
        line-height: 27px; }

    &__image {
        background: url(../img/footer-bg.png);
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        background-size: 100%;
        background-repeat: no-repeat;
        margin: 0 auto;
        background-position: center bottom; }


    &__block {
        width: 100%;
        margin: 0 auto;
        padding: 25px 0px 25px;
        background-color: rgba(132, 157, 147, 0.8);
        height: 100%; }

    &__nav {
        text-align: center;

        & > ul {
            display: flex;
            list-style-type: none;
            justify-content: center;
            li {
                margin-right: 30px;
                &:last-child {
                    margin-right: 0; }
                a {
                    font-size: 13px;
                    text-transform: uppercase;
                    color: #ffffff;
                    text-decoration: none; } } } } }


.social-list {
    text-align: center;
    margin-top: 15px;
    &__item {
        display: inline-block;
        margin-right: 15px;
        &:last-child {
            margin-right: 0; } } }

