.products {
    padding: 25px 15px 90px;

    &__block {
        display: flex;
        align-items: start; }

    input[type='text'] {
        display: inline-block;
        width: 65px;
        height: 25px;
        border: 1px solid #B3874C;
        border-radius: 4px;
        line-height: 25px;
        font-family: 'Avenir Next Regular';
        font-weight: 300;
        color: #323232;
        text-align: center;
        background-color: rgba(255,255,255, 0.5);
        font-size: 14px; }



    h2 {
        margin-bottom: 12px; }

    .block {
        margin-bottom: 25px; }


    .categories {
        &__item {
            position: relative;
            margin-bottom: 5px; } }

    &__list {
        display: flex;
        flex-wrap: wrap;
        align-items: start;
        justify-content: flex-start;
        width: 100%;
        @include r(1250) {
            justify-content: space-around; } } }

.product-preview {

    &__wrap {
        display: flex;
        flex-direction: column;
        justify-content: center; } }

.image-block {
    overflow: hidden;
    img {
        transition: transform 350ms cubic-bezier(0.55, 0, 0.1, 1); } }

.hover-block {
    &__wrap {
        max-width: 210px;
        width: 100%;
        margin: 20px auto 0;

        a {
            width: 100%; } }

    &__item {
        font-size: 14px;
        color: #404040;
        line-height: 1.5; } }

.filters {
    min-width: 250px;
    max-width: 390px;
    margin-right: 40px;
    @include r(992) {
        max-width: none; }

    &__title {
        i {
            display: none; } }

    .rotate-arrow {
        transform: rotate(225deg); } }

.impedance {
    &__item {
        display: flex;
        align-items: center;

        span {
            font-size: 17px;
            color: #323232; } }

    &__switch {
        position: relative;
        display: inline-block;
        width: 48px;
        height: 20px;
        border: none;
        margin: 0 15px;
        top: -2px; }

    &__slider {
        position: absolute;
        cursor: pointer;
        top: 2px;
        left: 0;
        right: 0;
        bottom: 0;
        -webkit-transition: .4s;
        transition: .4s;
        height: 20px;
        border: 2px solid #B3874C;
        border-radius: 2px;

        &:after {
            content: '';
            width: 13px;
            height: 7px;
            position: absolute;
            top: 2px;
            left: 2px;
            border: 2px solid #fff;
            border-top: none;
            border-right: none;
            background: transparent;
            opacity: 1;
            -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
            transition: .4s; }


        &:before {
            position: absolute;
            content: "";
            height: 100%;
            width: 50%;
            left: 0px;
            background-color: #B3874C;
            -webkit-transition: .4s;
            transition: .4s; } } }

.ports, .request-form {
    &__element {
        position: relative;
        margin-bottom: 5px; } }

.request-form {
    &__description {
        font-size: 15px; }
    &__elements {
       & > div {
            display: flex;
            justify-content: flex-start;
            input {
                height: 100%;
                width: auto; } } } }

.range-slider {
    position: relative;
    width: 93%;
    display: inline-block;
    padding: 0;
    margin-top: 0;

    span {
        color: #323232;
        font-size: 10px;
        font-weight: 300; }

    .right, .left {
        margin-top: 20px; }

    .left {
        float: left; }
    .right {
        float: right; } }


.filters::-webkit-scrollbar {
  background-color: #F3F3F3;
  width: 5px; }


.filters::-webkit-scrollbar-track {
  width: 5px;
  background-color: #F3F3F3; }


.filters::-webkit-scrollbar-thumb {
  width: 5px;
  height: 20px;
  background-color: #326197; }

#products-lists {
     max-width: 1350px; }

.upperfilter {
    @include r(768) {
        padding: 0 0 25px 0; }
    &__row {
        height: 28px; }
    &__col {
        height: 23px;
        text-align: center;
        min-width: 65px;
        position: relative;
        vertical-align : middle;
        text-align:center {} }
    &__row + &__row {
        border-top: 1px solid #efefef; }
    th {
        border-right: 1px solid #efefef; }
    &__col + &__col {
        border-left: 1px solid #efefef; }
    &__title {
        font-weight: 600;
        color: #2a6b46; }
    &__col &__checkbox {
        left: 0;
        right: 0;
        top: 3px;
        margin: auto; }
    &__header {
        text-align: left;
        margin-bottom: 15px;
        @include r(768);
        font-size: 18pt; } }

.table-border {
    border: 2px solid #b3874c;
    border-radius: 5px;
    max-width: 1200px;
    overflow-x: auto;
    @include r(725) {
        overflow-x: scroll; } }

.bottom-legend {
    text-align: left;
    padding: 5px 10px; }

.legend {
    color: #2a6b46;
    font-weight: 600; }

.legend-description {
    font-size: 11pt; }

.ports-title {
    font-weight: 400; }

.reset-btn {
    float: right;
    background: url(../img/reset.svg) no-repeat 2%;
    padding: 0 0 0 25px;
    background-size: 15%;
    color: #b3874c;
    &:hover {
        cursor: pointer; } }

.table-collapse {
    float: right;
    width: 20px;
    height: 20px;
    background-size: 20%;
    background: url(../img/arrow.svg) no-repeat 2%;
    &:hover {
        cursor: pointer; } }

.expanded-table {
    display: block; }

.collapsed-table {
    display: none; }

.rotated-arrow {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg); }
