.related-block {
    padding: 25px 15px 0px;

    .container {
        position: relative; }

    &__title {
        margin-bottom: 48px; }
    &__items {
        display: flex;
        justify-content: center;
        padding-bottom: 10px;
        @include rmin(635) {
            .slick-slide {
                margin: 0 15px; } }
        @include r(635) {
            .slick-slide {
                text-align: center;
                margin: 0 15px; } } }
    &__items > .slick-list {
        width: 100%; }
    &__items > .slick-list > .slick-track {
        width: 100%; }
    &__item {
        min-width: 285px;
        width: 100%;
        box-shadow: 0px 0px 5px 0px rgba(0,0,0, .16);

        .item {
            &__image-block {
                height: 180px;
                display: flex;
                align-items: center;
                justify-content: center; }

            &__image {
                max-width: 100%;
                height: auto; }

            &__name {
                color: #404040;
                font-size: 19px;
                line-height: 21px; }

            &__description {
                font-size: 14px;
                color: #A1A1A1;
                line-height: 21px; }

            &__block-text {
                border-top: 4px solid #E8E8E8;
                padding: 12px 20px; } } }
    @include r(1330) {
        .arrow-left {
            top: initial;
            bottom: 135px;
            left: -5px;
            z-index: 2; }
        .arrow-right {
            top: initial;
            bottom: 135px;
            right: -12px;
            z-index: 2; } } }

