.menu {
    cursor: pointer;
    position: absolute;
    right: 15px;
    top: 50%;
    width: 38px;
    height: 26px;
    display: none;
    z-index: 9;
    transform: translateY(-50%);

    &-global {
        backface-visibility: hidden;
        position: absolute;
        left: 0;
        border-top: 2px solid white;
        width: 100%;
        transition: 0.55s; }

    &-top {
        top: 0; }

    &-middle {
        top: 12px; }


    &-bottom {
        top: 24px; }


    &-top-click {
        backface-visibility: hidden;
        top: 15px;
        transform: rotate(50deg);
        transition: 0.55s 0.5s; }



    &-middle-click {
        opacity: 0; }



    &-bottom-click {
        backface-visibility: hidden;
        top: 15px;
        transform: rotate(-410deg);
        transition: 0.55s 0.5s; } }

