.demo {
  padding: 25px 15px;

  &__video {
    float: right;
    margin-left: 27px;
    margin-bottom: 10px;
    height: 300px;
    width: 45%;
    @include r(925) {
      float: initial;
      margin: 0 auto 30px;
      text-align: center;
      width: 100%;
      max-width: 550px; }
    iframe {
      width: 100%;
      height: 100%; } }

  &__text {
    p {
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0; } } }

  &__button {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
    @include r(925) {
      flex-direction: column;
      align-items: center;
      a {
        margin-bottom: 15px; }
      a:last-child {
        margin-bottom: 0; } } } }

.services {
  padding: 25px 15px;

  &__block {
    display: flex;
    justify-content: space-between;
    @include r(925) {
      flex-wrap: wrap;
      justify-content: center; }
    & > div {
      max-width: 530px;
      width: 100%;
      display: flex;
      flex-direction: column;
      height: 100%;
      position: relative;

      & > div {
        &:last-child {
          text-align: center; } }

      &:first-child {
        margin-right: 25px;
        @include r(925) {
          margin-right: 0; } }
      @include r(925) {
        margin-bottom: 30px;
        &:last-child {
          margin-bottom: 0; } }

      p {
        @include r(925) {
          min-height: auto !important;
          text-align: justify; } }

      a {
        margin-top: 30px;
        background-color: rgba(184, 150, 94, 0.2); } } } }
