.comment-navigation {
  display: block;
  &:after {
    display: block; } }

#respond:after {
  display: block; }

ol.commentlist {
  width: 100%;
  margin-top: 70px;
  padding: 0;
  ul {
    padding: 0;
    margin: 0 0 0 20px; } }

li {
  &.authorcomment, &.comment {
    padding: 20px 0 0 10px;
    list-style: none;
    border-top: 2px solid #808080; } }

ul.children {
  ul {
    margin-left: 20px; }
  li {
    border-right: none; } }

.vcard img {
  float: left;
  background: #c4c4c4;
  padding: 5px; }

.comment-navigation {
  .newer a, .older a {
    padding: 5px; } }

.comment-meta, ol.commentlist li small, p.subscribe-to-comments label {
  position: relative; }

.comment-meta, ol.commentlist li small {
  top: 10px;
  left: 10px; }

.reply, .comment-navigation .newer {
  float: right; }

.reply {
  background: #b3874c; }

.comment-navigation {
  .newer a, .older {
    background: #b3874c; } }

.reply {
  margin: 0 10px 10px 0; }

a.comment-reply-link, .reply a:link {
  display: inline-block;
  text-align: center;
  cursor: pointer;
  color: #fff; }

.comment-navigation {
  .newer a, .older a {
    display: inline-block;
    text-align: center;
    cursor: pointer;
    color: #fff; } }

a.comment-reply-link, .reply a:link {
  padding: 5px 0; }

a.comment-reply-link, .reply a:link {
  width: 70px; }

a.comment-reply-link:hover, .reply a:hover, .comment-navigation .newer a:hover, a.comment-reply-link:hover, .reply a:hover {
  background-color: #cc9a57;
  transition: background-color 350ms cubic-bezier(0.55, 0, 0.1, 1); }

.comment-navigation {
  margin: 10px 0 10px 0;
  &:after {
    content: ".";
    height: 0;
    visibility: hidden; } }

#respond:after {
  content: ".";
  height: 0;
  visibility: hidden; }

.clear {
  clear: both; }

.comment-navigation {
  .newer a:hover, .older a:hover {
    padding: 4px; } }

p.subscribe-to-comments {
  input {
    outline: 0;
    margin: 5px 3px 3px 3px !important;
    border: 0; }
  label {
    top: -2px;
    color: #666; } }
