.tabs {
    width: 100%;
    display: inline-block; }

.tab-links:after {
    display: block;
    clear: both;
    content: ''; }

.tab-links {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    @include r(425) {
        flex-direction: column;
        align-items: flex-start; } }

.tab-links li {
    margin: 0;
    float: left;
    list-style: none; }

.tab-links a {
    padding: 10px;
    display: inline-block;
    border-radius: 3px 3px 0px 0px;
    font-size: 16px;
    font-weight: 600;
    color: #4c4c4c;
    transition: all linear 0.15s;
    border-bottom: 1px solid rgba(135, 135, 135, 0.5); }

.tab-links a:hover {
    background: #a7cce5;
    text-decoration: none; }

li.active a, li.active a:hover {
    border-bottom: 3px solid red;
    color: #4c4c4c; }

.tab-content {
    padding-top: 15px; }

.tab {
    display: none; }

.tab.active {
    display: block; }
