.post {
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-content: flex-start;
    @include r(1200) {
      flex-direction: column;
      align-items: center; } }
  &__nav {
    margin-bottom: 45px;
    width: 100%;
    display: flex;
    justify-content: space-between;

    a {
      background-color: rgba(184, 150, 94, 0.2); } }

  &__title {
    margin-bottom: 30px;
    width: 100%;
    h3 {
      margin-bottom: 0;
      text-align: left; }
    span {
      font-size: 17px;
      color: #A1A1A1; } }

  &__description {
    max-width: 850px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include r(1200) {
      margin-bottom: 30px;
      max-width: 100%; }

    p {
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0; } }

    .tech-library-image {
      max-width: 850px;
      width: 100%;
      box-shadow: 0px 0px 7px 0px rgba(0,0,0, .16);
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0; } } }

  &__text {
    margin-bottom: 50px;
    width: 100%;
    p {
      width: 100%; } }

  &__comments {
    width: 100%; } }

.related-posts {
  @include r(1200) {
    width: 100%; }

  &__list {
    display: flex;
    flex-direction: column;
    @include r(1200) {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center; }
    .product-preview {
      &--posts {
          display: block; } } } }
