.frost-sulivan {
    width: 100%;
    padding: 25px 15px;
    .container {
        max-width: 1200px; }

    &__item {
        display: flex;
        margin-bottom: 50px;
        @include r(1035) {
            flex-direction: column;
            align-items: center;
            max-height: none; }
        &:last-child {
            margin-bottom: 0; }
        &:nth-child(even) {
            .frost-sulivan__text {
                order: 2; } }
        img {
            max-width: 100%; }
        .img-container {
            background-color: #2d467a;
            flex-basis: 40%;
            display: flex;
            align-items: center;
            @include r(1035) {
                width: 100%;
                min-height: 350px;
                justify-content: center; } } }


    &__wrap {
        display: flex;
        align-items: stretch; }

    &__text {
        background-color: #2D467A;
        text-align: center;
        display: flex;
        align-items: center;
        padding: 20px 32px 50px 32px;
        position: relative;
        flex-direction: column;
        justify-content: center;
        flex-basis: 70%;
        &:after {
            content: '';
            position: absolute;
            height: 2px;
            width: 184px;
            background-color: #B3874C;
            border-radius: 7px;
            left: 50%;
            transform: translateX(-50%);
            bottom: 40px;
            @include r(1035) {
                display: none; } }
        @include r(1035) {
            order: 2; }
        p {
            font-size: 12pt;
            line-height: 27px;
            color: #fff;
            word-wrap: break-word;
            position: relative;
            width: 100%;
            @include r(1035) {
                max-height: initial;
                text-align: center;
                padding: 25px 25px; } } }

    &__img {}

    &__line {
        position: absolute;
        height: 2px;
        width: 184px;
        background-color: #B3874C;
        border-radius: 7px;
        left: 50%;
        transform: translateX(-50%);
        bottom: 35px; }
    .reward-title {
        font-size: 25px;
        margin-bottom: 10px;
        @include r(925) {
          text-align: center; }
        @include r(1035) {
          text-align: center;
          padding: 0; } }
    .reward-date {
      position: absolute;
      bottom: 5px;
      left: 0;
      font-size: 13px;
      opacity: 0.5;
      @include r(925) {
        text-align: center; }
      @include r(1035) {
        text-align: center;
        padding: 0; } }

    &__main-text {
        font-size: 12pt;
        color: $text-color;
        line-height: 27px;
        padding-bottom: 30px; } }
