.positions {
  &__slider {
    display: flex;
    max-width: 1200px;
    margin: 0 auto;
    position: relative;
    text-align: center;

    .slick-track {
      margin: 0; } }

  &__block {
    width: 100%; }

  &__info {
    & > div {
      opacity: 0;
      height: 0;
      overflow: hidden;
      transition: opacity .7s cubic-bezier(0.55, 0, 0.1, 1), height 0ms cubic-bezier(0.55, 0, 0.1, 1);
      &.active-position {
        opacity: 1;
        height: 100%;
        transition: opacity .7s cubic-bezier(0.55, 0, 0.1, 1), height 0ms cubic-bezier(0.55, 0, 0.1, 1); } } }

  img {
    max-width: 335px; }

  .slick-slide {
    margin: 0 15px;
    max-height: 240px;
    display: flex;
    min-height: 240px;
    align-items: center;
    text-align: center;
    justify-content: center; }
  .arrow-slider {
    bottom: 40%; } }


.position {
  box-shadow: 0px 0px 7px 0px rgba(0,0,0,.16);
  &__all {
    box-shadow: 0px 0px 7px 0px rgba(0,0,0,.16); }

  &__title {
    height: 100px;
    background-color: #326197;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    h4 {
      font-size: 19px;
      line-height: 21px;
      font-weight: 900;
      color: #fff;
      margin-bottom: 0; } }

  &__filter {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
    p {
      font-size: 14px;
      line-height: 21px;
      margin-right: 10px;
      color: #636363; } }

  &__full {
    box-shadow: 0 0 7px 0 rgba(0,0,0,.16);
    display: none;

    .position__title {
      a {
        margin: 0; } }

    .position__detail {
      text-align: right; }

    .position__detail {
      p {
        color: #fff; } } }
  &__buttons {
    display: flex;
    padding: 20px;
    justify-content: space-between;
    align-items: center;

    a {
      font-size: 12pt; }

    .btn {
      font-size: 13px;
      margin: 0; } }

  &__text {
    padding: 20px;
    p {
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0px; } } }

  &__unit {
    background-color: rgba(255,255,255, 0.5);
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-bottom: 1px solid #E6E6E6;
    align-items: center;
    &:last-child {
      border-bottom: none; }

    @include r(415) {
      flex-direction: column;
      align-items: left;

      .position__button {
        margin-top: 15px; } } } }
