.webinar {
    .container {
        padding-top: 30px; }

    &__item {
        display: flex;
        @include r(1035) {
            flex-direction: column; }
        img {
            height: auto;
            max-width: 500px;
            @include r(1035) {
                margin: 0 auto;
                max-width: 100%; } } }

    &__wrap {
        padding: 40px;
        @include r(1035) {
            padding: 15px;
            text-align: center; } }

    .webinar-title {
        margin-bottom: 20px;
        text-align: left;
        @include r(1035) {
            text-align: center; } }
    .webinar-date {
        opacity: 0; }

    .webinar-description {
        padding: 15px 0;
        @include r(1035) {
            text-align: left; } }

    .webinar-btn {
        text-align: left;
        margin-bottom: 15px; }

    .webinar-video {
        padding: 20px 0px;
        text-align: center;
        iframe {
            width: 100%;
            max-width: 545px;
            height: 300px; } } }
.webinar-image-wrap {
    height: 100%; }
