.webinars {
  .container {
    position: relative; }

  @include r(1340) {
    .arrow-left {
      left: -5px;
      bottom: 50%;
      top: initial; }
    .arrow-right {
      right: -5px;
      bottom: 50%;
      top: initial; }

    margin: 0;
    margin-left: 10px;
    img {
      margin: 0 5px; } }

  &__cards {
    padding-top: 50px;
    @include r(1340) {
      padding-bottom: 30px; }
    .slick-slide {
      padding: 0 5px;
      @include r(1340);
      margin: 0 10px;
      @include r(480) {
        display: flex;
        justify-content: center; } }
    .webinar-date {
      opacity: 0; }
    .product-preview__text {
      text-align: center; }
    .product-preview__title {
      text-align: left;
      height: 63px;
      position: relative; }
    .hover-block {
      text-align: left;
      margin: 10px 0; }
    .product-preview__description {
      text-align: left; } }

  &__filters {
    display: flex; }

  &__block {
    display: flex;
    justify-content: space-between;
    @include r(1050) {
      flex-direction: column;
      align-items: center;
      text-align: center; }

    & > div {
      width: 100%; } }

  &__video {
    margin-right: 20px;
    @include r(1050) {
      margin-bottom: 30px;
      margin-right: 0; }
    p {
      margin-top: 30px; } }

  &__iframe {
    height: 285px; }

  &__button {
    text-align: center; }

  &__text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &.webinars--upcoming {
      justify-content: flex-start;

      strong {
        display: block; }

      & > div {
        display: flex;
        align-items: center;
        @include r(1050) {
          flex-direction: column;
          margin-bottom: 30px;
          &:last-child {
            margin-bottom: 0; } } } }

    p {
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0; } }
    @include r(1050) {
      margin-bottom: 30px;
      text-align: justify; } }

  &__description {
    span {
      opacity: 0; } }

  &__unit {
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0; } }

  &__register {
    margin-left: auto;
    @include r(1050) {
      margin-left: initial;
      margin-top: 15px; }
    a {
      background-color: rgba(184, 150, 94, 0.2); } }

  iframe {
  	  width: 100%;
  	  max-width: 545px;
  	  max-height: 295px;
  	  min-height: 200px;
  	  height: 100%; } }

.webinars-page {
  .slider-big {
    padding-bottom: 0; }
  .webinars {
    padding-top: 0;
    p {
      height: 100%; } } }
