$max-width: 1200px;
$max-width-sm: 1000px;
$white: #ffffff;
$main-blue-color: #1A53B7;
$title-color: #636363;
$text-color: #414141;

$border-color: #1851B6;


$section-padding: 25px 15px;


@mixin placeholder() {
  input::-webkit-input-placeholder {
    @content; }
  input:-moz-placeholder {
    @content; }
  input::-moz-placeholder {
    @content; }
  input:-ms-input-placeholder {
    @content; } }
