.drivers {
  .container {
    display: flex;
    flex-wrap: wrap;
    @include r(700) {
      flex-direction: column; } }

  &__text-container {
    flex-basis: 70%;
    color: $text-color;
    font-size: 12pt;
    line-height: 27px;
    padding-right: 30px;
    @include ie {
      padding-right: 0; }
    ul {
      padding-left: 70px; } }

  &__title {
    text-align: left; }

  &__btns {
    padding-top: 70px;
    flex-basis: 30%;
    flex-direction: column;
    a {
      width: 100%;
      margin-bottom: 15px; }
    a:last-child {
      margin-bottom: 0; }
    @include r(700) {
      padding-top: 30px; } }

  &-images {
    background-image: url(../img/text-bg-about.png);
    background-position: center center;
    background-repeat: no-repeat;
    h2 {
      text-align: left; }

    &__block {
      display: flex;
      justify-content: space-around;
      align-items: center;

      .img-title {
        font-weight: bold; }

      & > a {
        max-width: 25%;
        margin: 0 15px 15px;
        text-align: center;
        @include r(925) {
          max-width: 300px; } }

      @include r(768) {
        flex-wrap: wrap;
        align-items: center; } } } }



.accardion {
  &__block {
    background-color: rgba(255,255,255,0.5);
    box-shadow: 0px 0px 7px 0px rgba(0,0,0, .16);
    display: flex;
    @include r(850) {
      flex-direction: column; } }

  &__titles {
    border-right: 1px solid #E6E6E6;
    width: 495px;
    @include r(850) {
      border-bottom: 1px solid #e6e6e6;
      border-right: none;
      width: 100%;
      text-align: center; } }

  &__description {
    width: 100%; }


  &__item {
    display: flex;
    flex-wrap: wrap;
    height: 100%;
    transition: opacity 1s linear, height 0s step-start;
    @include r(590) {
      flex-direction: column; } }


  &__unit {
    display: flex;
    align-items: center;
    width: 50%;
    justify-content: center;
    flex-direction: column;
    padding: 30px 0px;
    border-right: 1px solid #E6E6E6;
    @include r(590) {
      width: 100%;
      height: 100%;
      min-height: 150px;
      border-bottom: 1px solid #E6E6E6;
      &:last-child {
        border-bottom: none; } }
    p {
      margin-bottom: 15px;
      text-align: center;
      width: 100%; } }

  &__title {
    padding: 20px;
    font-size: 21px;
    line-height: 21px;
    color: #636363;
    cursor: pointer;
    border-bottom: 1px solid #E6E6E6;
    transition: background-color 350ms cubic-bezier(0.55, 0, 0.1, 1), color 350ms cubic-bezier(0.55, 0, 0.1, 1);
    &:last-child {
      border-bottom: none; }
    &:hover {
      background-color: #B3874C;
      color: #fff;
      transition: background-color 350ms cubic-bezier(0.55, 0, 0.1, 1), color 350ms cubic-bezier(0.55, 0, 0.1, 1); } } }
