.testimonials-page {
  &__text {
    h3 {
      margin-bottom: 10px; } } }

.testimonials-image {
  text-align: center; }

.microwave {

  &__block {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include r(1215) {
      justify-content: center;
      flex-direction: column;
      align-items: center; }
    & > div {
      display: flex; } }
  &__text {
    p {
      position: relative;
      height: 160px;
      text-overflow: ellipsis;
      overflow: hidden;
      &:after {
        content: '';
        position: absolute;
        right: 0;
        bottom: 0;
        width: 20%;
        height: 20px; } } }

  &__videos {
    justify-content: flex-start;
    @include r(1215) {
      justify-content: center;
      margin-bottom: 30px; }
    @include r(825) {
      flex-direction: column;
      align-items: center;
      margin-bottom: 0px; } }


  &__video {
    margin-right: 15px;
    iframe {
      height: 100%; }
    @include r(1200) {
      width: 100%;
      text-align: center; }
    img {
      height: 100%; }
    @include r(825) {
      margin-right: 0; } }

  &__images {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    iframe {
      margin-bottom: 15px;
      &:last-child {
        margin-bottom: 0; } }

    @include r(825) {
      margin-top: 15px;
      margin-bottom: 30px;
      width: 100%;
      align-items: center;
      iframe {
        width: 100%;
        margin-bottom: 15px;
        &:last-child {
          margin-bottom: 0; } }
      img {
        margin-right: 0px;
        margin-bottom: 15px; } } }


  &__description {
    flex-direction: column;
    align-items: center;
    max-width: 385px;
    justify-content: space-between;
    width: 100%; }

  &__text {
    width: 100%;

    p {
      width: 100%; } } }
