.slider-big {
    width: 100%;
    padding: 25px 15px;

    .container {
        max-width: 1000px;
        margin: 0 auto; }


    &__slide {
        img {
            max-height: 157px;
            height: auto;
            margin: 0 auto; } }

    &__wrap {
        text-align: right; }

    &__arrows {
        text-align: right;
        margin-top: 20px; }


    &__arrow {
        width: 10px;
        height: 18px;
        display: inline-block;
        cursor: pointer;
        background-repeat: no-repeat; }

    .arrow-left {
        background-image: url(../img/small-arrow-slider-left.png); }


    .arrow-right {
        background-image: url(../img/small-arrow-slider-right.png); } }

